.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  padding: 60px 0;

  @include xl {
    padding: 58px 0;
  }

  @include md {
    padding: 44px 0;
  }

  @include xs {
    padding: 36px 0;
  }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {}

  &__nav {
    @include sm {
      position: fixed;
      top: 0;
      right: 0;
      transform: translateX(-100%);
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      background: #171D1F;
      padding: 120px 20px 20px;
      z-index: 5;
      transition: 0.3s;
      overflow: auto;

      &.active {
        transform: translateX(0);
      }
    }

    ul {
      display: flex;
      align-items: center;
      margin: 0 -22px;

      @include xl {
        margin: 0 -19px;
      }

      @include sm {
        display: block;
        text-align: center;
        margin: 0;
      }
    }

    li {
      padding: 0 22px;

      &:last-of-type {
        a {
          font-weight: 500;
          color: $colorOrange;
        }
      }

      @include xl {
        padding: 0 19px;
      }

      @include sm {
        &:not(:last-of-type) {
          padding: 0 0 20px;
        }
      }
    }

    a {
      font-family: $font-family-pfdin;
      font-style: italic;
      font-weight: 300;
      font-size: 40px;
      color: $colorWhite;

      &:hover {
        text-decoration: underline;
      }

      @include xl {
        font-size: 28px;
      }

      @include sm {
        font-weight: 500;
        font-size: 38px;
      }
    }
  }

  &__burger {
    display: none;

    @include sm {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      flex-direction: column;
      flex-shrink: 0;
      width: 36px;
      height: 29px;
      background: transparent;
      z-index: 6;
      cursor: pointer;
      border: none;
      position: relative;
      margin: 0 0 0 auto;

      span {
        background: $colorWhite;
        width: 100%;
        height: 2px;
        margin: 11px 0;
        border-radius: 10px;
        pointer-events: none;
        transition: 0.3s;
      }

      &::before,
      &::after {
        content: '';
        background: $colorWhite;
        width: 100%;
        height: 2px;
        border-radius: 10px;
        transition: 0.3s;
      }

      &.active {
        span {
          transform: scale(0);
        }

        &::before {
          width: 45px;
          position: absolute;
          top: 13px;
          transform: rotate(45deg);
          background: $colorOrange;
        }

        &::after {
          width: 45px;
          position: absolute;
          bottom: 14px;
          transform: rotate(-45deg);
          background: $colorOrange;
        }
      }
    }
  }
}
