.drink {
  position: relative;
  padding: 155px 0 160px;

  @include xl {
    padding: 124px 0 111px;
  }

  @include lg {
    padding: 109px 0 102px;
  }

  @include md {
    padding: 80px 0 0;
  }

  @include sm {
    padding: 70px 0 0;
  }

  @include xs {
    padding: 58px 0 0;
  }

  &--reverse {
    .drink__img {
      right: -420px;
      left: auto;
    }

    .drink__wrap {
      margin: 0 auto 0 0;
    }
  }

  .subtitle {
    font-size: 67px;

    @include xl {
      font-size: 48px;
    }

    @include lg {
      font-size: 45px;
    }

    @include md {
      font-size: 36px;
    }

    @include sm {
      font-size: 32px;
    }

    @include xs {
      font-size: 26px;
      margin: 8px 0 12px 0;
    }
  }

  &__alc {
    position: absolute;
    right: 115px;
    bottom: 75px;

    @include xl {
      width: 100px;
      height: auto;
      right: 85px;
      bottom: 56px;
    }

    @include xs {
      width: 85px;
      right: 38px;
      bottom: 38px;
    }
  }

  &__wrap {
    max-width: 770px;
    width: 100%;
    margin: 0 50px 0 auto;

    @include xl {
      max-width: 553px;
      margin: 0 42px 0 auto;
    }

    @include lg {
      max-width: 522px;
    }

    @include md {
      max-width: 100%;
    }
  }

  &__info {
    margin: 22px 0 73px;

    @include xl {
      margin: 25px 0 55px;
    }

    @include lg {
      margin: 25px 0 42px;
    }

    @include md {
      margin: 15px 0 30px;
    }

    @include sm {
      margin: 12px 0 25px;
    }

    @include xs {
      margin: 8px 0 19px;
    }
  }

  &__btn {
    min-width: 350px;

    @include xl {
      min-width: 247px;
    }

    @include lg {
      min-width: 238px;
    }

    @include xs {
      padding: 0 31px;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
    object-fit: cover;
    max-width: 1438px;
    width: 100%;
    height: 100%;

    &--right {
      left: auto;
      right: 0;
    }

    @include xl {
      max-width: 1028px;
    }

    @include md {
      position: static;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto;
      max-width: 100%;
      margin: 0 0 0 150px;
    }

    @include xs {
      margin: 0;
    }
  }
}
