.games {
  background: $colorOrange;
  padding: 75px 0 130px;
  overflow: hidden;

  @include xl {
    padding: 54px 0 93px;

    .container {
      max-width: 1088px;
    }
  }

  @include lg {
    padding: 50px 0 84px;

    .container {
      max-width: 1042px;
    }
  }

  @include md {
    padding: 45px 0 84px;
  }

  @include xs {
    padding: 35px 0 48px;
  }

  &--start {
    @include xs {
      padding: 35px 0 86px;
    }
  }

  &--grey {
    background: #D8D8D8;

    .title {
      color: #2C2C2C;
    }

    .games__slider-btn {
      svg {
        stroke: #6A6A6A;
      }

      &:hover {
        svg {
          stroke: #F9F9F9;
        }
      }
    }
  }

  .title {
    font-size: 90px;
    text-align: center;
    margin: 0 0 50px;

    @include xl {
      font-size: 64px;
      margin: 0 0 38px;
    }

    @include lg {
      font-size: 60px;
      margin: 0 0 36px;
    }

    @include md {
      font-size: 52px;
      margin: 0 0 30px;
    }

    @include sm {
      font-size: 44px;
      margin: 0 0 28px;
    }

    @include xs {
      font-size: 38px;
      margin: 0 0 25px;
    }
  }



  &__slider {
    position: relative;
    margin: 0 -8px -10px;

    @media (max-width: 1100px) {
      padding: 0 30px;
    }
  }

  .swiper {
    padding: 0 8px 10px;
  }

  &__item {
    min-height: 258px;
    background: $colorWhite;
    border: 1px solid #AFAFAF;
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.25);
    border-radius: 40px;
    padding: 50px 48px 32px;
    margin: 0 auto;

    @include xl {
      min-height: 178px;
      border-radius: 28px;
      padding: 26px 30px 21px;
    }

    @include lg {
      min-height: 173px;
    }

    @include md {
      max-width: 270px;
      padding: 30px 20px 25px;
    }

    @include xs {
      border-radius: 41px;
      padding: 37px 12px 25px;
    }
  }

  &__item-wrap {
    min-height: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: $font-family-pfdin;
    font-weight: 500;
    font-size: 48px;
    color: #606060;
    margin: 0 0 14px;

    img {
      max-height: 120px;
      object-fit: cover;

      @include xl {
        max-width: 75px;
      }

      @include lg {
        max-width: 70px;
      }

      @include md {
        max-width: 80px;
      }

      @include xs {
        max-width: 100px;
      }

      @media (max-width: 355px) {
        max-width: 80px;
      }
    }

    @include xl {
      min-height: 97px;
      font-size: 37px;
      margin: 0 0 5px;
    }

    @include lg {
      min-height: 89px;
    }

    @include md {
      font-size: 32px;
    }

    @include sm {
      font-size: 28px;
    }

    @include xs {
      min-height: 100px;
      font-size: 24px;
      margin: 0 0 32px;
    }
  }

  &__item-date {
    font-family: $font-family-pfdin;
    font-style: italic;
    font-weight: 300;
    font-size: 32px;
    color: #606060;
    text-align: center;

    @include xl {
      font-size: 23px;
    }

    @include md {
      font-size: 22px;
    }

    @include sm {
      font-size: 20px;
    }

    @include xs {
      font-size: 18px;
    }
  }


  &__slider-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;
    cursor: pointer;

    svg {
      width: 34px;
      height: 65px;
      fill: transparent;
      stroke: #F9F9F9;
      transition: stroke .3s;

      @include xl {
        width: 28px;
        height: auto;
      }

      @media (max-width: 1100px) {
        width: 26px;
      }

      @include md {
        width: 24px;
      }

      @include sm {
        width: 23px;
      }

      @include xs {
        width: 21px;
      }
    }

    &:hover {
      svg {
        stroke: #6A6A6A;
      }
    }
  }

  &__slider-prev {
    left: -100px;

    @include xl {
      left: -59px;
    }

    @media (max-width: 1100px) {
      left: -10px;
    }
  }

  &__slider-next {
    right: -100px;

    @include xl {
      right: -59px;

      @media (max-width: 1100px) {
        right: -10px;
      }
    }
  }

}
