.raffle {
  background: linear-gradient(180deg, rgba(15, 23, 26, 0.88) 0%, rgba(15, 23, 26, 0) 100%);
  padding: 139px 0 145px;

  @include xl {
    padding: 100px 0;
  }

  @include lg {
    padding: 93px 0;
  }

  @include md {
    padding: 70px 0;
  }

  @include sm {
    padding: 60px 0;
  }

  @include xs {
    padding: 46px 0 39px;
  }

  .container {
    max-width: 1252px;
    text-align: center;

    @include xl {
      max-width: 967px;
    }

    @include lg {
      max-width: 873px;
    }
  }

  &__overhead {
    font-family: $font-family-pfdin;
    font-style: italic;
    font-weight: 300;
    font-size: 48px;
    color: $colorOrange;
    text-transform: uppercase;
    margin: 0 0 10px;

    @include xl {
      font-size: 34px;
      margin: 0 0 7px;
    }

    @include lg {
      font-size: 32px;
      margin: 0 0 5px;
    }

    @include md {
      font-size: 28px;
    }

    @include sm {
      font-size: 24px;
    }

    @include xs {
      font-size: 20px;
      margin: 0 0 9px;
    }
  }

  .title {
    font-size: 120px;

    @include xl {
      font-size: 85px;
    }

    @include lg {
      font-size: 70px;
    }

    @include md {
      font-size: 56px;
    }

    @include sm {
      font-size: 48px;
    }

    @include xs {
      font-size: 40px;
    }
  }

  .subtitle {
    @include xs {
      margin: 9px 0 0;
    }
  }

  &__info {
    margin: 18px 0 89px;

    @include xl {
      margin: 21px 0 62px;
    }

    @include lg {
      margin: 16px 0 53px;
    }

    @include md {
      margin: 16px 0 52px;
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin: 0 0 85px;

    @include xl {
      margin: 0 0 59px;
    }

    @include md {
      display: block;
      margin: 0 0 38px;
    }
  }

  &__item {
    width: calc(33.33% - 120px * 2 / 3);

    img {
      @include xl {
        max-width: 210px;
        height: auto;
        margin: 0 auto;
      }

      @include lg {
        max-width: 199px;
      }

      @include md {
        max-width: 180px;
      }

      @include xs {
        max-width: 176px;
      }
    }

    @include lg {
      width: calc(33.33% - 55px * 2 / 3);
    }

    @include md {
      width: 100%;

      &:not(:last-of-type) {
        margin: 0 0 54px;
      }
    }
  }

  &__item-name {
    font-family: $font-family-pfdin;
    font-style: italic;
    font-weight: 300;
    font-size: 35px;
    line-height: 1.3;
    text-transform: uppercase;
    margin: 50px 0 0;

    @include xl {
      font-size: 25px;
      margin: 37px 0 0;
    }

    @include lg {
      font-size: 22px;
    }

    @include md {
      margin: 28px 0 0;
    }

    @include xs {
      line-height: 1.1;
      margin: 22px 0 0;
    }
  }

  &__btns {
    @include xs {
      display: flex;
      flex-direction: column;
      padding: 0 27px;
    }
  }

  &__btn {
    &.btn {
      min-height: 70px;
      min-width: 340px;

      @include xl {
        min-height: 52px;
        min-width: auto;
        padding: 0 30px;
      }

      @include lg {
        min-height: 50px;
      }

      @include xs {
        font-size: 14px;
        min-height: 48px;
      }
    }

    &--transparent {
      border: 4px solid #61C248;
      background: transparent;
      margin: 0 0 0 15px;

      &:hover {
        border-color: darken($color: #61C248, $amount: 5%);
      }

      &:active {
        border-color: darken($color: #61C248, $amount: 2%);
      }

      @include xs {
        margin: 22px 0 0;
      }
    }
  }
}
