.snacks {
  position: relative;
  padding: 159px 0 190px;

  @include xl {
    padding: 112px 0 133px;
  }

  @include lg {
    padding: 112px 0 123px;
  }

  @include md {
    padding: 80px 0 0;
    background: #1C2A2D;
  }

  @include sm {
    padding: 70px 0 0;
  }

  @include xs {
    padding: 54px 0 0;
  }

  &--border {
    border-bottom: 38px solid $colorOrange;

    @include md {
      border-bottom: none;
    }
  }


  &__wrap {
    max-width: 790px;
    width: 100%;
    margin: 0 0 0 -30px;

    @include xl {
      margin: 0 0 0 -8px;
    }

    @include md {
      margin: 0;
    }

    &--right {
      margin: 0 -40px 0 auto;

      @include xl {
        max-width: 550px;
        margin: 0 0 0 auto;
      }
    }
  }

  .subtitle {
    @include xs {
      margin: 9px 0 0;
    }
  }

  &__info {
    margin: 22px 0 62px;

    span {
      color: $colorOrange;
    }

    @include xl {
      margin: 24px 0 46px;
    }

    @include lg {
      margin: 16px 0 37px;
    }

    @include xs {
      margin: 13px 0 24px;
    }
  }

  &__btn {
    min-width: 350px;

    @include xl {
      min-width: 245px;
    }

    @include lg {
      min-width: 232px;
    }

    @include md {
      position: relative;
      z-index: 1;
    }

    @include xs {
      padding: 0 30px;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    object-fit: cover;
    width: 100%;
    height: 100%;

    @include md {
      position: static;
      left: auto;
      bottom: auto;
      margin: -30px 0 0;
    }
  }
}
