.map {
  min-height: 842px;
  position: relative;
  text-align: center;
  padding: 78px 0 0;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
    background: #17181A;
  }

  @include xl {
    min-height: 596px;
    padding: 57px 0 0;
  }

  @include lg {
    min-height: 554px;
  }

  @include md {
    min-height: 450px;
  }

  @include sm {
    min-height: auto;
    background: #17181A;
    padding: 57px 0 86px;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 78px;
      background: linear-gradient(180deg, rgba(15, 23, 26, 0.88) 0%, rgba(15, 23, 26, 0) 100%);
    }
  }

  @include xs {
    padding: 28px 0 86px;
  }



  .title {
    position: relative;
    z-index: 1;

    @include sm {
      margin: 0 0 46px;
    }

    @include xs {
      font-size: 40px;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include sm {
      position: static;
      max-width: 600px;
      margin: 0 auto;
    }
  }
}
