.rules {

  &__title {
    margin: 0 0 40px;

    @include lg {
      font-size: 68px;
      margin: 0 0 30px;
    }

    @include md {
      font-size: 54px;
      margin: 0 0 25px;
    }

    @include sm {
      font-size: 42px;
      margin: 0 0 18px;
    }

    @include xs {
      font-size: 32px;
      margin: 0 0 13px;
    }
  }

  &__info {
    font-size: 16px;
    height: 68vh;
    padding: 0 70px 0 0;

    @include lg {
      font-size: 14px;
      padding: 0 60px 0 0;
    }

    @include md {
      font-size: 13px;
      padding: 0 50px 0 0;
    }

    @include sm {
      font-size: 12px;
      padding: 0 40px 0 0;
    }

    @include xs {
      font-size: 10px;
      padding: 0 35px 0 0;
    }

    p {
      margin: 0 0 27px;

      @include lg {
        margin: 0 0 23px;
      }

      @include md {
        margin: 0 0 18px;
      }

      @include sm {
        margin: 0 0 14px;
      }

      @include xs {
        margin: 0 0 12px;
      }
    }

    .simplebar-track.simplebar-vertical {
      width: 4px;
      background: #4F4F4F;
      border-radius: 90px;
    }

    .simplebar-scrollbar::before {
      left: 0;
      right: 0;
      background: #FD6913;
      opacity: 1;
      border-radius: 90px;
    }

    .simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
      top: 0;
      bottom: 0;
    }
  }
}
