.wow {
  visibility: hidden;
}

.logo {
  display: inline-block;
  font-weight: 600;
  font-size: 27px;
  padding: 15px 40px;
  border: 4px solid $colorOrange;
  border-radius: 90px;

  @include xl {
    font-size: 20px;
    padding: 11px 25px;
  }

  @include lg {
    font-size: 18px;
    border: 2px solid $colorOrange;
    padding: 8px 27px;
  }

  @include md {
    font-size: 16px;
    padding: 8px 22px;
  }

  @include xs {
    border: 1px solid $colorOrange;
    font-size: 14px;
    padding: 7px 18px;
  }
}

.title {
  font-weight: 500;
  font-size: 110px;
  font-family: $font-family-pfdin;
  font-style: italic;
  line-height: 1;
  text-transform: uppercase;

  @include xl {
    font-size: 78px;
  }

  @include lg {
    font-size: 74px;
  }

  @include md {
    font-size: 64px;
  }

  @include sm {
    font-size: 54px;
  }

  @include xs {
    font-size: 44px;
  }
}

.subtitle {
  font-weight: 500;
  font-size: 67px;
  font-family: $font-family-pfdin;
  font-style: italic;
  line-height: 1.19;
  color: $colorOrange;
  text-transform: uppercase;

  @include xl {
    font-size: 48px;
    line-height: 1;
  }

  @include lg {
    font-size: 45px;
  }

  @include md {
    font-size: 38px;
  }

  @include sm {
    font-size: 32px;
  }

  @include xs {
    font-size: 26px;
  }
}

.btn {
  min-width: 424px;
  min-height: 94px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 600;
  font-size: 28px;
  color: $colorWhite;
  background: #61C248;
  border: none;
  padding: 0 40px;
  border-radius: 90px;
  cursor: pointer;
  transition: background .3s;

  &:hover {
    background: darken($color: #61C248, $amount: 5%);
  }

  &:active {
    background: darken($color: #61C248, $amount: 2%);
  }

  @include xl {
    min-height: 66px;
    font-size: 19px;
    min-width: 304px;
  }

  @include lg {
    min-height: 64px;
    font-size: 18px;
    min-width: 287px;
  }

  @include md {
    min-height: 60px;
  }

  @include sm {
    min-height: 55px;
    font-size: 16px;
  }

  @include xs {
    min-width: auto;
    min-height: 48px;
    font-size: 14px;
  }
}

.swal2-shown .swal2-title {
  font-size: 16px;
  font-weight: 400;
  padding: 1.25em 1.25em 0;
}

.swal2-shown .swal2-styled.swal2-confirm {
  min-width: 150px;
  background: #61C248;
  color: $colorWhite;
  border-radius: 90px;
  box-shadow: none;

  &:hover {
    background: darken($color: #61C248, $amount: 5%);
  }

  &:active {
    background: darken($color: #61C248, $amount: 2%);
  }
}

.swal2-shown .swal2-styled.swal2-confirm:focus {
  box-shadow: none;
}
