.hero {
  position: relative;
  min-height: 940px;
  display: flex;
  align-items: flex-end;
  padding: 0 0 75px;

  @include xl {
    min-height: auto;
    padding: 203px 0 54px;
  }

  @include lg {
    padding: 191px 0 49px;
  }

  @include md {
    display: block;
    padding: 150px 0 0;
  }

  @include sm {
    padding: 130px 0 0;
  }

  @include xs {
    padding: 113px 0 0;
  }

  &--acount {
    min-height: 927px;
    align-items: center;
    padding: 226px 0 83px;

    .hero__wrap {
      @media (max-width: 1260px) {
        max-width: 400px;
        width: 100%;
      }

      @include md {
        max-width: 100%;
      }

      .title {
        @include xs {
          font-size: 38px;

          br {
            display: none;
          }
        }
      }

      .subtitle {
        font-size: 55px;

        @include xl {
          font-size: 40px;
          margin: 0 0 11px;
        }

        @include md {
          font-size: 32px;
        }

        @include sm {
          font-size: 30px;
        }

        @include xs {
          font-size: 26px;
          margin: 11px 0;
        }
      }
    }

    .hero__img {
      bottom: 0;

      @media (max-width: 1024px) {
        max-width: 700px;
      }

      @include md {
        max-width: 100%;
      }

      @include xs {
        max-width: 130%;
        margin: 4px -34px -2px;
      }
    }

    @include xl {
      padding: 174px 0 80px;
    }

    @include lg {
      padding: 166px 0 82px;
    }
  }

  &--acount2 {
    padding: 289px 0 15px;
    margin: 0 0 -145px;

    @include xl {
      padding: 205px 0 15px;
      margin: 0 0 -112px;
    }

    @include lg {
      padding: 192px 0 82px;
      margin: 0 0 -164px;
    }

    @include md {
      margin: 0;
    }

    .container {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;

      @include md {
        display: block;
      }
    }

    .hero__wrap {
      flex-shrink: 0;

      @media (max-width: 1150px) {
        margin: -45px 0 0;
      }

      @include md {
        margin: 0;
      }
    }

    .hero__wrap .subtitle {
      @include lg {
        font-size: 37px;
      }
    }

    .hero__info {
      margin: 4px 0 67px;

      @include xl {
        font-size: 18px;
        margin: 4px 0 46px;
      }

      @include xs {
        font-size: 12px;
        margin: 4px 0 30px;
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(15, 23, 26, 0.88) 0%, rgba(15, 23, 26, 0) 100%);
  }

  .container {
    height: 100%;
  }

  &__wrap {

    .subtitle {
      margin: -9px 0 14px;

      @include xl {
        margin: 0 0 29px;
      }

      @include lg {
        margin: 0 0 25px;
      }

      @include xs {
        margin: 10px 0 26px;

        br {
          display: none;
        }
      }
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &.active {
      .hero__input--tel {
        display: none;
      }

      .hero__input--text {
        display: block;
      }
    }
  }

  &__input {
    max-width: 424px;
    width: 100%;
    height: 95px;
    font-family: $font-family-pfdin;
    font-weight: 300;
    font-size: 48px;
    color: $colorWhite;
    border: 3px solid #FFFFFF;
    border-radius: 90px;
    background: transparent;
    padding: 0 50px;
    text-align: left;
    margin: 0 0 20px -26px;

    &::placeholder {
      color: $colorWhite;
    }

    @include xl {
      max-width: 304px;
      height: 69px;
      font-size: 32px;
      padding: 0 35px;
      margin: 0 0 15px;
    }

    @include lg {
      max-width: 286px;
      height: 66px;
      border: 2px solid #FFFFFF;
      margin: 0 0 12px;
    }


    @include md {
      height: 60px;
      font-size: 30px;
      margin: 0 0 20px;
    }

    @include sm {
      text-align: center;
      height: 55px;
    }

    @include xs {
      height: 48px;
      font-size: 28px;
      max-width: 100%;
    }

    &--text {
      font-size: 44px;
      text-align: center;
      display: none;
    }
  }

  &__info {
    margin: 4px 0 115px;

    @include xl {
      margin: 4px 0 82px;
    }

    @include lg {
      margin: 4px 0 64px;
    }

    @include md {
      margin: 4px 0 50px;
    }

    @include sm {
      margin: 4px 0 40px;
    }

    @include xs {
      margin: 4px 0 30px;
    }
  }

  &__btn {
    margin: 0 0 0 -26px;

    @include xl {
      margin: 0;
    }

    @include xs {
      font-size: 14px;
      padding: 0 38px;
    }
  }

  &__form-btn {
    @include xs {
      width: 100%;
    }
  }

  &__checkbox {
    cursor: pointer;

    input:checked+.hero__checkbox-info .hero__checkbox-square:before {
      opacity: 1;
      visibility: visible;
    }
  }

  &__checkbox-info {
    display: flex;
    align-items: center;
    font-size: 20px;
    margin: 50px 0 0;

    a {
      color: $colorOrange;
      text-decoration: underline;
      margin: 0 7px;

      @include xl {
        margin: 0 4px;
      }
    }

    @include xl {
      margin: 35px 0 0;
      font-size: 12px;
    }

    @include lg {
      margin: 33px 0 0;
    }

    @include xs {
      font-size: 10px;
      margin: 23px 0 0;
    }
  }

  &__checkbox-square {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border: 2px solid #6A6A6A;
    flex-shrink: 0;
    margin: 0 16px 0 19px;

    &::before {
      content: '';
      width: 24px;
      height: 24px;
      background: $colorOrange;
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s, visibility .3s;

      @include xl {
        width: 20px;
        height: 20px;
      }

      @include lg {
        width: 18px;
        height: 18px;
      }

      @include xl {
        width: 18px;
        height: 18px;
      }

      @include md {
        width: 16px;
        height: 16px;
      }

      @include xs {
        width: 14px;
        height: 14px;
      }
    }

    @include xl {
      width: 24px;
      height: 24px;
    }

    @include lg {
      width: 22px;
      height: 22px;
      margin: 0 14px 0 18px;
    }

    @include md {
      width: 20px;
      height: 20px;
      margin: 0 19px 0 0;
    }

    @include sm {
      width: 19px;
      height: 19px;
    }

    @include xs {
      width: 18px;
      height: 18px;
      margin: 4px 19px 0 10px;
    }
  }

  &__img {
    position: absolute;
    right: 6px;
    bottom: -20px;
    z-index: -1;
    object-fit: cover;
    max-width: 1259px;

    @include xl {
      right: 0;
      bottom: -15px;
      max-width: 891px;
    }

    @include lg {
      max-width: 834px;
    }

    @include md {
      position: static;
      right: auto;
      bottom: auto;
      max-width: 100%;
      margin: 0 0 -2px;
    }

    @include xs {
      max-width: 130%;
      margin: 22px -34px -2px;
    }
  }

  &__img-beer {
    max-width: 995px;
    width: 100%;
    margin: -80px -174px 0 0;

    @media (max-width: 1700px) {
      margin: -56px -110px 0 0;
    }

    @include lg {
      margin: -56px -101px 0 0;
    }

    @media (max-width: 1150px) {
      margin: 35px -20px 0 0;
    }

    @include md {
      max-width: 700px;
      margin: 0 auto;
    }

    @media (max-width: 450px) {
      width: auto;
      margin: 0 -30px 0 -18px;
    }

    img {
      width: 100%;
    }
  }
}
