.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background: #171D1F;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include md {
      padding: 60px 20px 30px 50px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    max-width: 832px;
    width: 100%;

    @include md {
      width: 100%;
    }
  }

  &__close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 60px;
    height: 60px;
    border: none;
    background: transparent;
    cursor: pointer;

    @include md {
      top: -50px;
    }

    @include xs {
      width: 45px;
      height: 45px;
    }

    &::before,
    &::after {
      content: '';
      background: $colorOrange;
      width: 60px;
      height: 2px;
      left: 0;
      border-radius: 10px;
      transition: .3s;

      @include xs {
        width: 45px;
      }
    }

    &::before {
      position: absolute;
      top: 29px;
      transform: rotate(45deg);

      @include xs {
        top: 21px;
      }
    }

    &::after {
      position: absolute;
      bottom: 29px;
      transform: rotate(-45deg);

      @include xs {
        bottom: 22px;
      }
    }

    &:hover {

      &::before,
      &::after {
        background: $colorWhite;
      }
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}
