.footer {
  background: $colorOrange;
  padding: 55px 0;

  @include xl {
    padding: 38px 0;
  }

  @include md {
    padding: 30px 0;
  }

  @include sm {
    display: none;
  }

  .container {
    max-width: 1740px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include xl {
      max-width: 1244px;
    }

    @include lg {
      max-width: 1170px;
    }
  }

  &__logo.logo {
    border-color: $colorWhite;
  }

  &__nav {

    ul {
      display: flex;
      align-items: center;
      margin: 0 -40px;

      @include xl {
        margin: 0 -19px;
      }
    }

    li {
      padding: 0 40px;

      @include xl {
        padding: 0 19px;
      }

      &:last-of-type {
        a {
          font-weight: 500;
        }
      }
    }

    a {
      font-family: $font-family-pfdin;
      font-style: italic;
      font-weight: 300;
      font-size: 40px;
      color: $colorWhite;

      &:hover {
        text-decoration: underline;
      }

      @include xl {
        font-size: 28px;
      }
    }
  }
}
